import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen'; 
import {AgmCoreModule} from '@agm/core';
import { IonicStorageModule } from '@ionic/storage';
import { SqlStorage } from '../providers/sql-storage/sql-storage';
import { UserSettings } from '../providers/user-settings/user-settings';
import { MyTeamsPage, TeamDetailPage, TeamsPage, GamePage,MapPage, TournamentsPage,TeamHomePage, StandingsPage } from '../pages/pages';
  

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = MyTeamsPage;
  pages: Array<{title: string, component: any}>;

  constructor(
  public platform: Platform, 
  public statusBar: StatusBar, 
  public splashScreen: SplashScreen, 
  private userSettings : UserSettings) {
  this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide(); 
      this.userSettings.initStorage().then(() => this.rootPage = MyTeamsPage);
    });
  }
  
  openPage(page) {
      this.nav.setRoot(page.component);
  }
  
 goHome() {
this.nav.pop() ;
}
goToTournaments() {
  this.nav.push(TournamentsPage);
}  
}



